
  @import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;700&family=Pattaya&display=swap');
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: 'Inter', sans-serif;
  overflow-x: hidden;
}
.Loader {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  background: rgb(255 255 255 / 50%);
  z-index: 1;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}


